import React from "react";
import {connect} from "react-redux";
import PropTypes from "prop-types";
import { getSelectedVariant} from "eshop/modules/simfree/selectors";

class ProductDetailsProductSpecification extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
    }

    toHTML(content) {
        return {__html: content};
    }

    renderValue(element){
        if(element.value==='true'){
            return "Tak";
        }
        if(element.value==='false'){
            return "Nie";
        }
        return element.value;
    }
    renderUnit(element, i){
        if(!element.featureUnit) {
            return '';
        }
        return element.featureUnit.symbol;
    }

    renderOneRow(element){
        return (<tr key={"key_"+element.name}>
            <td>{element.name!=null?element.name:element.code}</td>
            <td>{element.featureValues.map((feat, i) => {return this.renderValue(feat)+" "+this.renderUnit(element, i);})}</td>
        </tr>)
    }
    renderOneColumn(element,key){
        if(!element)
            return '';
        return (<table className="opl-table opl-details-table opl-table--width__50-50 u-small-spacing-l u-medium-spacing-xl">
            <thead>
            <tr key={"tr"+key}>
                <th colSpan="2" className="u-padding-top">
                    <h3 className="h4 u-text-left">{element.name!=null?element.name:element.code}</h3>
                </th>
            </tr>
            </thead>
            <tbody>
            {element.features.map((feature, i) => {return this.renderOneRow(feature)})}
            </tbody>
        </table>)
    }
    renderDoubleColumnClassification(classifications, i){
        return (<div key={"classification-"+i} className={i===0?"opl-gradient-fade--item":"opl-gradient-fade--item u-spacing-top-xl" +
            " u-medium-no-spacing u-small-no-spacing"}>
            <div className="l-row">
                <div className="l-col l-col-small-12 l-col-medium-12 l-col-6 ">
                    {this.renderOneColumn(classifications[i],i)}
                </div>
                <div className="l-col l-col-small-12 l-col-medium-12 l-col-6 ">
                    {this.renderOneColumn(classifications[i+1],i+1)}
                </div>
            </div>
        </div>);

    }

    render(){
        let classification = this.props.deviceData.classification;
        if(classification && classification.length) {
            const selectedVariant=this.props.deviceData.variantList.filter(variant => {return variant.productId === this.props.selectedVariant})[0];
            const eanCode = !!selectedVariant && selectedVariant.ean;
            const index = classification.map(function(item) { return item.code; }).indexOf('ean');
            if (eanCode) {
                const eanObject = {
                    features: [{code: "ean", name: "Ean", featureValues: [{value: eanCode}]}],
                    code: "ean",
                    name: "Inne informacje"
                };
                index > 0 ? classification[index] = eanObject : classification.push(eanObject);
            } else {
                classification = index > 0 ? classification.filter(item => {return item.code !== 'ean'}): classification;
            }
        }
        return (
            <div className="js-expander__container opl-wiking-expander__container" hidden={!classification || classification.length === 0}>
                <h2 id={'product-details'} data-skiplinks={'Specyfikacja produktu'} className="h2 u-padding-l u-small-no-padding-b u-small-padding-left-xl u-no-spacing">Specyfikacja</h2><a href="#" className="js-expander__trigger opl-wiking-expander__trigger"><span className="g-icon g-icon--only g-icon--arrow-down-slim"></span><span className="u-acc-hide">rozwiń</span></a>
                <div className="js-expander__content opl-wiking-expander__content u-spacing-xl">
                    <div className="u-small-padding-top-l u-spacing-xl">
                        <div id={'product-specification-expander'}  data-js-module="common/modules/opl-gradient-fade" data-js-options="{&quot;hideTrigger&quot;: false, &quot;triggerHideName&quot;: &quot;Zwiń&quot;, &quot;triggerShowName&quot;: &quot;Więcej&quot;, &quot;showFirst&quot;: 0, &quot;scrollSet&quot;: 80 }" data-js-conditions="element:{was seen}" className="opl-gradient-fade">
                            <div className="opl-gradient-fade--wrapper">
                                <div className="opl-gradient-fade--cover">
                                </div>
                                {classification && classification.map((element,i) => {return i%2==0?this.renderDoubleColumnClassification(classification,i):''})}
                            </div>
                            <div className="opl-gradient-fade--trigger u-spacing-top"><a href="#" className="opl-load-more u-font-bold">Więcej</a></div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    selectedVariant: getSelectedVariant(state)
});
const mapDispatchToProps = dispatch => ({
});

export default  connect(mapStateToProps, mapDispatchToProps)(ProductDetailsProductSpecification);